.ajax-container[data-loading='true']{
  position:relative; }

.ajax-container[data-loading='true']:before{
  position:absolute;
  content:'';
  background-size:contain;
  background-image:url(../images/spinner2.eec4eed90a.svg);
  width:150px;
  height:150px;
  left:50%;
  top:50%;
  -webkit-transform:translate(-50%, -50%);
      -ms-transform:translate(-50%, -50%);
          transform:translate(-50%, -50%);
  z-index:99; }

.ajax-container[data-loading='true']:after{
  position:absolute;
  content:'';
  width:100%;
  height:100%;
  left:0;
  top:0;
  z-index:90;
  background-color:rgba(255, 255, 255, 0.6); }

#woo_bg_eu_vat p{
  margin:12px !important; }

.wc-eu-vat-table{
  width:100%;
  margin:5px 0; }

.wc-eu-vat-table td,
.wc-eu-vat-table th{
  text-align:left;
  padding:7px 12px; }

.eu-vat-overview{
  margin:0; }

.column-eu_vat{
  width:10%;
  color:#555; }

.column-eu_vat ul li{
  color:#555; }

.woocommerce table.form-table .mx-datepicker input[type=text]{
  width:100%; }

.woocommerce table.form-table .multiselect{
  width:400px; }

.woocommerce table.form-table .multiselect .multiselect__option--highlight{
  background:#007cba; }

.woocommerce table.form-table .field-error{
  color:red; }

.multiselect__tags{
  padding:0; }

.multiselect__tags input{
  border:0; }

.multiselect__tags .multiselect__single{
