.multiselect__placeholder{
  padding:10px 40px 0 12px; }
.form-message{
  margin-left:15px;
  font-size:17px;
  font-weight:500; }

i.woo-bg-icon{
  width:31px;
  height:30px;
  background-size:contain;
  display:inline-block;
  margin:0 7px;
